export default {
  data() {
    return {
      text: '',
      textVisible: true,
    };
  },
  methods: {
    type() {
      const message = "Hi, 안녕하세요! ";
      let i = 0;
      const interval = setInterval(() => {
        this.text += message[i];
        i++;
        if (i > message.length - 1) {
          clearInterval(interval);
        }
      }, 100);
    },
  },
  mounted() {
    this.type();
  },
};
